<template>
  <div :class="$style.mask" :style="styleObj">
    <slot/>
  </div>
</template>

<script>
import Image from '@/assets/bck.png'
export default {
  name: 'v-mask',
  computed: {
    styleObj () {
      return {
        backgroundImage: `url(${Image})`
      }
    }
  }
}
</script>

<style module>
.mask{
  width: 375px;
  height: 188px;
  position: relative;
  background-position: center;
  background-size: cover;
}
</style>



