<template>
  <div>
    <v-header :title="loginname" @click-right="showAction">
      <i class="iconfont icon-more" :class="$style.icon" slot="right"></i>
    </v-header>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <div :class="$style.waper">
        <v-mask>
          <v-avatar :avatar="avatar" :class="$style.center" @click-avatar="goGitHub"/>
        </v-mask>
        <van-tabs v-model="active" :class="$style.recent">
          <van-tab title="最近主题">
            <x-topic v-for="item in topics" :key="item.id" :topicInfo="item"></x-topic>
          </van-tab>
          <van-tab title="最近评论">
            <x-topic v-for="item in replys" :key="item.id" :topicInfo="item"></x-topic>
          </van-tab>
        </van-tabs>
      </div>
    </van-pull-refresh>
    <van-actionsheet v-model="show" :actions="actions" />
  </div>
</template>

<script>
import { Tab, Tabs, Actionsheet } from 'vant'
import XTopic from '@/components/XTopic'
import Header from '@/base/Header.vue'
import Mask from '@/components/Mask.vue'
import Avatar from '@/components/Avatar.vue'
import { fetchUser } from '../../util/api'
export default {
  name: 'x-user',
  components: {
    [Header.name]: Header,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Actionsheet.name]: Actionsheet,
    [Mask.name]: Mask,
    [Avatar.name]: Avatar,
    [XTopic.name]: XTopic
  },
  data () {
    return {
      isLoading: false,
      userInfo: null,
      active: 0,
      show: false,
      actions: [{name: '查看收藏', callback: this.toCollect}, {name: '查看GitHub', callback: this.goGitHub}]
    }
  },
  methods: {
    onRefresh () {
      this.fetchUserInfo()
    },
    async fetchUserInfo () {
      try {
        this.$toast.loading('加载中...')
        const res = await fetchUser({loginname: this.loginname})
        if (res.success) {
          this.$toast.clear()
          this.isLoading = false
          this.userInfo = res.data
          return
        }
        this.$toast.clear()
        this.$toast.fail('网络错误？')
      } catch (error) {
        console.error(error)
        this.$toast.clear()
        this.isLoading = false
      }
    },
    goGitHub () {
      window.location.href = this.github
    },
    toCollect () {
      this.$router.push(`${this.$route.fullPath}/collect`)
    },
    showAction () {
      this.show = true
    }
  },
  created () {
    this.fetchUserInfo()
  },
  computed: {
    loginname () {
      return this.$route.params.name
    },
    github () {
      return `https://github.com/${this.userInfo.githubUsername}`
    },
    avatar () {
      if (this.userInfo) {
        return this.userInfo.avatar_url
      }
      return null
    },
    topics () {
      if (this.userInfo) {
        return this.userInfo.recent_topics
      }
      return []
    },
    replys () {
      if (this.userInfo) {
        return this.userInfo.recent_replies
      }
      return []
    }
  }
}
</script>

<style module>
.waper{
  width: 100%;
  min-height: calc(100vh -100px);
}
.center{
  position: absolute;
  left: 50%;
  top: 50%;
}
.info{
  width: 100%;
  margin-top: 20px;
}
.recent{
  background-color: #eeeeee;
}
.icon{
  color: #3388ff;
  font-size: 20px;
}
</style>



