<template>
  <div :class="$style.avatar" :style="styleObj" @click="$emit('click-avatar')">
  </div>
</template>

<script>
const AVATAR_SIZE = {
  'small': 30,
  'large': 80,
  'normal': 50
}
export default {
  name: 'v-avatar',
  props: {
    avatar: {
      type: String
    },
    size: {
      validator (v) {
        return Object.keys(AVATAR_SIZE).indexOf(v) !== -1
      },
      default: 'large'
    },
    postion: {
      type: String,
      default: 'center'
    }
  },
  computed: {
    styleObj () {
      let url
      const style = {
        width: this.currentSize + 'px',
        height: this.currentSize + 'px'
      }
      if (this.avatar) {
        url = this.avatar
      } else {
        url = 'https://avatars2.githubusercontent.com/u/0?v=4&s=120'
      }
      style.backgroundImage = `url(${url})`
      if (this.postion === 'center') {
        style.postion = 'absolute'
        style.left = `calc(50% - ${this.currentSize / 2 + 'px'})`
        style.top = `calc(50% - ${this.currentSize / 2 + 'px'})`
      }
      return style
    },
    currentSize () {
      return AVATAR_SIZE[this.size]
    }
  }
}
</script>

<style module>
.avatar{
  display: inline-block;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  position: absolute;
}
</style>



